@import "http://at.alicdn.com/t/font_paone2lkwsfko6r.css";
body {
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Microsoft Yahei",Roboto,"Helvetica Neue",Arial,sans-serif;
}
$color-primary:#00B9D8;
$color-text-gray: #9C9C9C;
$color-border-gray: #DCDCDC;
abbr[title]{
    border-bottom:none;
    text-decoration: none;
    cursor: default;
}
a{
    color:$color-primary;
    &:focus,&:hover{
        color: darken($color-primary, 5%);
    }
}
[type=checkbox], [type=radio] {

    transform: scale(1.5);
    transform-origin: left center;
}

hr {
    margin: 5px;   
}
header {
    background-color: $color-primary;
}

.mt5{
    margin-top: 5px !important;
}
.mt10{
    margin-top: 10px !important;
}
.mb10{
    margin-bottom: 10px !important;
}
.mt20{
    margin-top: 20px !important;
}
.mr10{
    margin-right: 10px !important;
}


.tag-wrapper{
    .tag{
        cursor: pointer;
        color:#333;
        font-weight: normal;
        font-size: inherit;
        display: inline-block;
        min-width:40px;
        border:1px solid #D9D9D9;
        border-radius:30px;
        padding:4px 10px;
        vertical-align: middle;
        &:nth-child(n+2){
            margin-left: 10px;
        }
        &.active{
            border-color:#66AFE9;
            background-color: $color-primary;
            color:white;
        }
    }
}
.link-wrapper{
    a:nth-child(n+2){
        margin-left: 8px;
    }
}
.btn{
    min-width: 140px;
}
.button-wrapper{
    .btn:nth-child(n+2){
        margin-left: 8px;
    }
}
.progress{
    margin-bottom: 0;
    height: 12px;
}
.progress[value]::-webkit-progress-value{
    background-color: $color-primary;
}
.progress[value]::-webkit-progress-bar{
    background-color: #99E3EF;
}

.bg-custom{
    background-color: $color-primary;
}

.navbar{
    border-radius:0;
    padding-top: 0;
    padding-bottom: 0;
    color:white;
    .navbar-nav .nav-link{
        padding: 1rem 1.5rem;
        color:white;
        &:hover{
            background-color: darken($color-primary, 10%);
            color:#FEC107;
        }
    }



}

.navbar-light .navbar-nav .open>.nav-link,
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{
    color:#FEC107 !important;
}
.navbar-light .navbar-nav .open>.nav-link, .navbar-light .navbar-nav .open>.nav-link:focus, .navbar-light .navbar-nav .open>.nav-link:hover{

    background-color: darken($color-primary, 10%);
}

.navbar-brand{
    width: 226px;
    height: 46px;
    background: url(../images/logo.png);
    background-size: 226px 46px;
    font-size: 0;
    margin-top: 4px;
    margin-right: 40px;
}
.navbar-nav .nav-item+.nav-item{
    margin-left: 5px;
}

.navbar-side{
    li{
        float:left;
        padding:.5rem 1rem;
        a{
            color:white;
            .iconfont{
                font-size: 1.5rem;
            }
            &:hover,&:focus{
                text-decoration: none;
            }
        }
    }
}
.dropdown-menu{
    font-size: .875rem;
    .menu-title{
        padding-left: 1.5rem;
        color:#333;
    }
    .menu-wrapper{
        text-indent: .5rem;
    }
}


.breadcrumb-text{
    font-size: 18px;
}


.book-title {
    background-color: #E9ECF3;
    h2{
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 0;
        padding:0rem 1rem;
        height: 56px;
        line-height: 56px;
        img {
            width: 40px;
            height: 40px;
            vertical-align: middle;
            display: inline-block;
        }
    }
}


html,body {
    height: 100%;
    min-width: 1280px;
}

.main-section>.container-fluid,
.book-title>.container-fluid,
.navbar>.container-fluid
{
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    background-color: #F2F3F5;
    .main-section{
        flex:1;
        >.container-fluid{
            height: 100%;
        }
        .main-row{
            height: 100%;
            display: flex;
            .nav-row{
                width: 250px;
                >.nav{
                    height: 100%;
                }
            }
            .content-row{
                flex:1;
                background-color: #fff;
                .content{
                    padding: 1rem;
                }
            }
        }
    }
}



.nav-pills .nav-link{border-radius:0;}
.nav-pills .nav-item.open .nav-link, .nav-pills .nav-item.open .nav-link:focus, .nav-pills .nav-item.open .nav-link:hover, .nav-pills .nav-link.active, .nav-pills .nav-link.active:focus, .nav-pills .nav-link.active:hover{
    color:$color-primary;
    background-color: #D2D7DD;
}
.nav-custom{
    background-color: #F7F8FA;
    .nav-item{
        border-bottom:1px solid #EAEBEF;
        margin-top: 0!important;
    }
    .nav-link{
        color:#1F1F1F;
        .iconfont{
            font-size: 1.4rem;
            vertical-align: -2px;
        }
        &:hover{
            background-color: darken(#F7F8FA,3%);
        }
    }
    .iconfont{
        display: inline-block;
        width: 30px;
    }

    .nav-item:nth-child(1) .iconfont{
        color:$color-primary;
    }
    .nav-item:nth-child(2) .iconfont{
        color:#FA8F6F;
    }
    .nav-item:nth-child(3) .iconfont{
        color:#3DC796;
    }
    .nav-item:nth-child(4) .iconfont{
        color:#618AFE;
    }
    .nav-item:nth-child(5) .iconfont{
        color:#CE82EE;
    }
}


.form-control-static{
    /* border-width:2px 0; */
}

.form-group{
    >div[class^="col-"]{
        position: relative;
    }
    .help-inline{
        position: absolute;
        right: 0;
        top: 50%;
        transform:translate(100%,-50%);
    }
    .help-block{
        font-size: 14px;
        margin-bottom: 0;
    }
    .col-form-label{
        text-align: right;
        line-height: 1;
    }
    &.has-required{
        .col-form-label:before{
            content:"*";
            color:red;
            display: inline-block;
            vertical-align: middle;
            margin-right: 5px;
        }
    }
    &.has-danger{
        .help-inline,.help-block{
            color:#D9534F;
        }
    }
}

.animation-spin{
    -webkit-animation: spin .8s linear infinite;
    display: inline-block;
    width: 16px;
    height: 16px;
    font-size: 0;
    line-height: 19px;
}

@-webkit-keyframes spin{
    0{
        -webkit-transform:rotate(0deg);
    }
    100%{
        -webkit-transform:rotate(360deg);
    }
}

@keyframes spin{
    0{
        transform:rotate(0deg);
    }
    100%{
        transform:rotate(360deg);
    }
}


.form-group-wrapper,
.form-group-static-wrapper{
    max-width: 700px;
}
.form-group-static-wrapper{
    margin-top: 20px;
    .form-group{
        margin-bottom: 0;
    }
    .form-control-static{
        color:#A9A9A9;
    }
}
.form-control-static{
    padding-top: .5rem;
    padding-bottom: .5rem;
    margin-bottom: 0;
    line-height: 1;
    border: solid transparent;
    border-width: 1px 0;
}

.title-custom{
    h3{
        font-size: 1rem;
        margin-bottom: 0;
        display: inline-block;
    }
    .option{
        margin-left: 10px;
    }
    padding-bottom: 1rem;
    border-bottom:1px solid #CCE4E8;
}


.card-custom{
    padding:10px 20px;
    margin: 20px 0 20px;
    /* max-width:600px; */
    .card-header{
        padding:10px 0;
        background-color: transparent;
        font-size: 18px;
        .option{
            float:right;
        }
    }
    .card-block{
        padding: 0;
        .text{
            padding: 5px 0;
        }
    }
    .card-footer{
        background-color: transparent;
        padding:10px 0;
    }
    &.card-success{
        background-color: #E2FEE5;
        border:1px solid darken(#E2FEE5,5%);
        .icon-success{
            color:#70D379;
        }
    }
    &.card-warning{
        background-color: #FAF3E1;
        border:1px solid #FDEFCC;
    }
    &.card-success,&.card-warning{
        /* max-width: 600px; */
        padding:0 20px;
    }
    &.card-custom-info .card-block p{
        display: flex;
        align-items: center;
    }
}

.drag-drop-holder{
    /* width: 100%; */
    /* height: 300px; */
    border:1px dashed $color-border-gray;
    text-align: center;
    /* line-height: 300px; */
    &>.cell{
        display: inline-block;
    }
    &.drag-drop-holder-empty{
        min-height: 150px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 10px;
        padding-right: 10px;
    }
    &.uploading{
        line-height: 1;
        text-align: left;
        border-color:$color-primary;
        display: flex;
        flex-direction:column;
        position: relative;
        padding-bottom: 40px;
        img {
            flex:1;
        }
        &>.cell{
            /* width: 190px; */
            position: relative;
        }
        .uploading-info{
            position: absolute;
            bottom:0;
    transform: translateY(100%);
            width: 100%;
            padding:4px 10px;
            background-color: #DEF6FA;
            display: flex;
            align-items: flex-end;
            .info{
                flex:1;
                font-size: 14px;
                margin-right: 10px;
                .file-size{
                    float:right;
                }
                .progress{margin-top: 3px;}
            }
        }
        .uploaded-info{
            position: absolute;
            bottom:0;
    transform: translateY(100%);
            width: 100%;
            font-size: 14px;
            padding: 4px 10px;
            text-align:left;
            .info{
                display: flex;
                /*
                .file-name,.file-size
                 */
span:nth-child(1){
    flex:1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
&:nth-child(2){
    color:$color-text-gray;
    font-size: 12px;
    margin-top: 5px;
}
            }

        }

        &.show{
            display: flex;
            flex-direction: row;
            border-color:transparent !important;
            .cell{
                margin-right: 45px;
                &:last-child{
                    margin-right: 0;
                }
            }
            .uploaded-info{
                padding: 4px 0;
            }
        }
    }
}




.btn-primary{
    background-color: $color-primary;
