    }
    &.table-hover tbody tr:hover{
        background-color: #EBF9FC;
    }
    &.table-vam{
        th,td{
            vertical-align: middle;
        }

    }
    .td-line + .td-line{
        margin-top: 5px;
    }
}


.table-vertical{
    display: flex;
    thead th{
        display: block;
        white-space:nowrap;
        text-align: right;
        border-bottom-width:1px;
    }
    tbody {
        display: flex;
        flex:1;
        tr{
            flex:1;
        }
