.lesson-cell-list{
    display: flex;
    &.lesson-cell-list-empty{
        .cell{
            &:hover{
                background-color: transparent;
            }
        }
    }
    .cell{
        flex:1;
        width:100%;
        margin: 0px auto;
        display: flex;
        border-bottom:1px solid $color-border-gray;
        padding:10px;
        &:hover{
            background-color: #DEF6FA;
            .mark-time-label{
                opacity:1;
            }
        }
        .number{
            width:40px;
            font-size: 20px;
        }
        .text{
            flex:1;
            padding-left: 20px;
        }
        .mark-time{
            span{
                display: inline-block;
                width: 70px;
                text-align: center;
            }
            span:nth-child(n+2){
                margin-left: 20px;
            }
        }
        .option{
            position: relative;
            .icon-sort{
                margin-right: 15px;
            }
        }
        .mark-time-label{
            font-size: 12px;
            position: absolute;
            right: 0;
            top: -12px;
            opacity:0;
            margin-bottom: 0;
            span{
                display: inline-block;
                width: 70px;
                text-align: center;
            }
            span:nth-child(n+2){
                margin-left: 20px;
            }
        }
    }
    &.lesson-cell-list-vertical{
        flex-direction:column;
    }
    .option{
        .btn{
            min-width:auto;
        }
        @at-root .dropdown-menu-right{
            right:0;
            left:auto;
        }
    }
}
.lesson-title{
    text-align: center;
    font-size: 18px;
    margin: 10px 0;
}

.lesson-edit-content{
