}
.photo-list{
    overflow-y:scroll;
    max-height: 500px;
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    columns: 30%;
    &>.cell{
        margin: 20px 20px 0px 0;
        position: relative;
        input[type="checkbox"]{
            opacity:0;
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 1;
        }
        &:nth-child(3n){
            margin-right: 0;
        }
        &.active{
            border:1px solid $color-primary;
            input[type="checkbox"]{
                opacity:1;
            }
        }
        &:hover{
            input[type="checkbox"]{
                opacity:1;
            }
        }
    }
}


.input-keyboard{
    position: absolute;
    right: 26px;
    top:2px;
    a{
        color:$color-text-gray;
        opacity:.5;
        text-decoration: none;
        &:hover{
            opacity:1;
            color:$color-primary;
        }
    }
    .iconfont{
        font-size: 30px;
        line-height: 1;
    }
    &:hover img,&:focus img{
        display: block;
    }
    img{
        display: none;
        position: absolute;
        z-index: 2;
        top: 125%;
        right:0;
    }
