audio::-webkit-media-controls-play-button, video::-webkit-media-controls-play-button {
    -webkit-appearance: none;
    border: 3px solid red;
    height: 30px;
    width: 30px;
    border-radius: 50%;
}

input[type='range'] {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    min-width:100px;
    height: 10px;
    background: none;
    font: 1em verdana;
    cursor: pointer;
    &:focus{
        outline:0;
    }
}


input[type='range']::-ms-track {
    box-sizing: border-box;
    border: none;
    color: transparent;
    background: transparent;
    position: relative;
    &:after{
        content:"";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 10px;
        background-color: red;

    }
}
input[type='range']::-ms-fill-lower {
    height: 0.125em;
    background: linear-gradient(90deg, $color-primary, $color-primary);
}

input[type='range']::-webkit-slider-runnable-track{
    position: relative;
    background:linear-gradient(90deg, #00B9D8 50%, transparent 0) no-repeat content-box,linear-gradient(90deg, #C1C1C1 100%, #C1C1C1 0) no-repeat content-box;
    background-size: 100% 50%, 100% 50%;
    background-position: 0 center;
}

input[type='range']::-webkit-slider-thumb{
    width: 10px;
    height: 10px;
    border-radius:100%;
    background-color:white;
    box-shadow:0px 1px 5px rgba(0,0,0,0.4);
    border:none;
}                

input[type='range']::-webkit-slider-runnable-track, input[type='range']::-webkit-slider-thumb, input[type='range'] {
    -webkit-appearance: none;
}


input[type="range"]::-moz-range-progress{
    position: relative;
    background:linear-gradient(90deg, #00B9D8 100%, transparent 0) no-repeat content-box;
    background-size:  100% 100%;
    height: 5px;
}
input[type="range"]::-moz-range-thumb{
    width: 10px;
    height: 10px;
    border-radius:100%;
    background-color:white;
    box-shadow:0px 1px 5px rgba(0,0,0,0.4);
    border:none;
}



.text-speaking{
