@import "http://at.alicdn.com/t/font_nhwj5mpnidx6r.css";

.ata-editor{
    font:16px/1.5 Arial, "Microsoft Yahei";
    border:1px solid #CCCCCC;
    position: relative;
    .toolbar{
        box-sizing: border-box;
        border-bottom:1px solid #CCC;
        padding: 3px;
        background-color: #F5F5F5;
        display: inline-flex;
        width: 100%;
        .icon-caret{
            font-size: 12px;
        }
        .icon-text-color{
            border-bottom: 2px solid red;
            padding-bottom: 2px;
        }
        .option{
            flex:1;
            .cell:nth-child(n+2){
                padding-left: 3px;
                margin-left: -2px;
                border-left:1px solid #CCCCCC;
            }
        }
        button{
            cursor: pointer;
            border:1px solid transparent;
            background-color: transparent;
            padding: 3px 5px;
            border-radius: 3px;
            outline:0;
            min-width:30px;
            font:inherit;
            &:hover,&:focus{
                border-color: #BCBCBC;
                background-color: #E9E9E9;
            }
        }
    }
    .edit-area{
        min-height: 500px;
        iframe{
            width: 100%;
            height: 100%;
        }
    }
    .menu-popup{
        position: absolute;
        border:1px solid #CCC;
        border-radius:3px;
        box-shadow:3px 3px 5px rgba(0,0,0,.3);
        background-color: white;
        overflow:hidden;
        hr{
            background-color: #ccc;
            height: 1px;
            margin: 0;
            border: 0;
        }
        .list{
            min-width:120px;
            &.list-image{
                max-width:300px;
                .images{
                    text-indent: 10px;
                    >span{
                        display: block;
                        cursor: pointer;
                    }
                }
                span:first-child{
                    &:hover{
                        background-color: transparent;
                        color:inherit;
                        cursor: default;
                    }
                }
            }
            &>span,&.list-image>.images>span{
                padding:5px 10px;
                display: block;
                margin-bottom: 0;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                &:hover{
                    background-color: #999999;
                    color:white;
                    cursor: pointer;
                }
            }
            .h1{
                font-size: 34px;
            }
            .h2{
                font-size: 30px;
            }
            .h3{
                font-size: 26px;
            }
            .h4{
                font-size: 22px;
            }
            .h5{
                font-size: 18px;
            }
            .h6{
                font-size: 14px;
            }
        }



        .color{
            display: flex;
            flex-wrap:wrap;
            width: 120px;
            justify-content: space-around;
            padding:5px;
            span{
                width: 20px;
                height: 20px;
                margin: 3px;
                cursor: pointer;
                border:1px solid transparent;
                &:hover{
                    border-color:#000;
                }
            }

            .black{background-color: black;}
            .gray{background-color: gray;}
            .silver{background-color: silver;}
            .maroon{background-color: maroon;}
            .red{background-color: red;}
            .fuchsia{background-color: fuchsia;}
            .purple{background-color: purple;}
            .olive{background-color: olive;}
            .navy{background-color: navy;}
            .blue{background-color: blue;}
            .yellow{background-color: yellow;}
            .orange{background-color: orange;}
            .green{background-color: green;}
            .lime{background-color: lime;}
            .teal{background-color: teal;}
            .aqua{background-color: aqua;}
        }
    }
